export default{
	onLoad() {
		
	},
	data(){
		return {
			city:['桐乡','乌鲁木齐'],
		}
	},
	methods:{
		backIndex(){
			uni.navigateBack({
				delta:1,
				animationType:'pop-out',
				animationDuration:300
			})
		},
		goIndex(val){
			uni.navigateTo({
				url: '../index/index?city='+val,
				animationType:'pop-out',
				animationDuration:300
			});
		},
		goSearch(){
			uni.navigateTo({
				url:'../search/search',
				animationType:'pop-in',
				animationDuration:300
			})
		}
	}
}
